import { MatTableDataSource } from '@angular/material/table';
import { BehaviorSubject, Observable, Subscription } from 'rxjs';
import { StateLog } from '../../services/states.service';

export class StateLogDataSource extends MatTableDataSource<StateLog> {
  private collection$: Subscription | undefined;
  private connectionCount = 0;

  constructor(private stateLog$: Observable<StateLog[]>) {
    super();
  }

  override connect(): BehaviorSubject<StateLog[]> {
    if (!this.collection$) {
      this.collection$ = this.stateLog$.subscribe((data) => (this.data = data));
    }
    this.connectionCount++;
    return super.connect();
  }

  override disconnect() {
    if (this.connectionCount) {
      this.connectionCount--;
      if (this.connectionCount === 0 && this.collection$) {
        this.collection$.unsubscribe();
        this.collection$ = undefined;
      }
    }
    super.disconnect();
  }
}
