import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { AfterViewInit, Component, ViewChild } from '@angular/core';
import { MatSort } from '@angular/material/sort';
import { StateLog, StateLogType, StatesService } from '../../services/states.service';
import { StateLogDataSource } from './state-log.data-source';

@Component({
  selector: 'ff-status-log',
  templateUrl: './status-log.component.html',
  styleUrls: ['./status-log.component.scss'],
  animations: [
    trigger('detailExpand', [
      state('collapsed', style({ height: '0px' })),
      state('expanded', style({ height: '*' })),
      transition(
        'expanded <=> collapsed',
        animate('250ms cubic-bezier(0.4, 0.0, 0.2, 1)')
      ),
    ]),
  ],
})
export class FutureFactoryStatusLogComponent implements AfterViewInit {
  readonly stateColumns = ['actions', 'received', 'type', 'module'];
  readonly logTypes = [StateLogType.CONNECTION, StateLogType.MODULE, StateLogType.FTS];
  readonly dataSource: StateLogDataSource;
  readonly modules$ = this.statesService.allModules$;
  selectedState: StateLog | null = null;
  private _selectedType: string | null = null;
  private _selectedModule: string | null = null;
  @ViewChild(MatSort) sort!: MatSort;

  constructor(private statesService: StatesService) {
    this.filterPredicate = this.filterPredicate.bind(this);

    this.dataSource = new StateLogDataSource(this.statesService.stateLog$);
    this.dataSource.filterPredicate = this.filterPredicate;
  }

  filterPredicate = (data: StateLog, filter: string): boolean => {
    const includesType = data.type === this._selectedType;
    const includesModule = data.state.serialNumber === this._selectedModule;
    if (this._selectedType && this._selectedModule) {
      return includesType && includesModule;
    }
    if (this._selectedType && !this._selectedModule) {
      return includesType;
    }
    if (!this._selectedType && this._selectedModule) {
      return includesModule;
    }
    return true;
  }

  get selectedType() {
    return this._selectedType;
  }

  set selectedType(type: string | null) {
    this._selectedType = type;
    // Update the filter for the data source
    //  otherwise the filterPredicate will not be triggered
    this.dataSource.filter = this.createFilter(this._selectedType, this._selectedModule);
  }

  get selectedModule() {
    return this._selectedModule;
  }

  set selectedModule(module: string | null) {
    this._selectedModule = module;
    // Update the filter for the data source
    //  otherwise the filterPredicate will not be triggered
    this.dataSource.filter = this.createFilter(this._selectedType, this._selectedModule);
  }

  /**
   * Creates a filter string for the data source, based on the selected type and module.
   * These are combined with a  character, which is not used in any of the data.
   * @param {string|null} type
   * @param {string|null} module
   * @returns {string}
   */
  private createFilter(type: string | null, module: string | null): string {
    return `${type ?? ''}${module ?? ''}`;
  }

  ngAfterViewInit(): void {
    this.sort.active = 'received';
    this.sort.start = 'desc';
    this.dataSource.sort = this.sort;
  }

  selectState(state: StateLog) {
    this.selectedState = this.selectedState === state ? null : state;
  }
}
